"use client";

import {
  Priority,
  Project,
  Task,
  useGetProjectsQuery,
  useGetTasksQuery,
} from "@/state/api";
import React from "react";
import { useAppSelector } from "@/app/(components)/redux";
import { DataGrid, GridColDef } from "@mui/x-data-grid";
import Header from "@/app/(components)/Header";
import {
  Bar,
  BarChart,
  CartesianGrid,
  Cell,
  Legend,
  Pie,
  PieChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts";
import { dataGridClassNames,dataGridSxStyles } from "../../../../lib/utils";

const taskColumns: GridColDef[] = [
  { field: "title", headerName: "Title", width: 200 },
  { field: "status", headerName: "Status", width: 150 },
  { field: "priority", headerName: "Priority", width: 150 },
  { field: "dueDate", headerName: "Due Date", width: 150 },
];

const COLORS = ["#3B82F6", "#10B981", "#F59E0B", "#EF4444"];
const BAR_COLORS = ["#60A5FA", "#34D399", "#FBBF24", "#F87171"]; // Distinct colors for bars

const HomePage = () => {
  const {
    data: tasks,
    isLoading: tasksLoading,
    isError: tasksError,
  } = useGetTasksQuery({ projectId: parseInt("1") });
  const { data: projects, isLoading: isProjectsLoading } =
    useGetProjectsQuery();

  const isDarkMode = useAppSelector((state) => state.global.isDarkMode);

  if (tasksLoading || isProjectsLoading) return (
    <div className="flex justify-center items-center h-screen text-xl font-semibold text-gray-600 dark:text-gray-300">
      Loading...
    </div>
  );
  if (tasksError || !tasks || !projects) return (
    <div className="flex justify-center items-center h-screen text-xl font-semibold text-red-600 dark:text-red-400">
      Error fetching data
    </div>
  );

  // Debug: Log projects with parsed endDate
  console.log("Projects:", projects.map(p => ({
    id: p.id,
    name: p.name,
    endDate: p.endDate,
    parsedEndDate: p.endDate ? new Date(p.endDate).toISOString() : null
  })));

  const priorityCount = tasks.reduce(
    (acc: Record<string, number>, task: Task) => {
      const { priority } = task;
      acc[priority as Priority] = (acc[priority as Priority] || 0) + 1;
      return acc;
    },
    {},
  );

  const taskDistribution = Object.keys(priorityCount).map((key) => ({
    name: key,
    count: priorityCount[key],
  }));

  const statusCount = projects.reduce(
    (acc: Record<string, number>, project: Project) => {
      const currentDate = new Date();
      const endDate = project.endDate ? new Date(project.endDate) : null;
      const status = endDate && endDate <= currentDate ? "Completed" : "Active";
      acc[status] = (acc[status] || 0) + 1;
      return acc;
    },
    {},
  );

  // Debug: Log status counts
  console.log("Status Count:", statusCount);
  const projectStatus = Object.keys(statusCount).map((key) => ({
    name: key,
    count: statusCount[key],
  }));
  console.log("Project Status:", projectStatus);

  const chartColors = isDarkMode
    ? {
        bar: "url(#barGradientDark)",
        barGrid: "#6B7280",
        pieFill: "#93C5FD",
        text: "#E5E7EB",
        tooltipBg: "#374151cc", // Semi-transparent
        tooltipText: "#F3F4F6",
        bg: "#1F2937",
        cardBg: "#374151",
      }
    : {
        bar: "url(#barGradientLight)",
        barGrid: "#D1D5DB",
        pieFill: "#34D399",
        text: "#111827",
        tooltipBg: "#FFFFFFcc", // Semi-transparent
        tooltipText: "#111827",
        bg: "#F9FAFB",
        cardBg: "#FFFFFF",
      };

  const CustomTooltip = ({ active, payload }: any) => {
    if (active && payload && payload.length) {
      return (
        <div
          className="rounded-xl shadow-lg p-4 border"
          style={{
            backgroundColor: chartColors.tooltipBg,
            color: chartColors.tooltipText,
            borderColor: isDarkMode ? "#4B5563" : "#E5E7EB",
          }}
        >
          <p className="font-semibold text-sm">{`${payload[0].name}: ${payload[0].value}`}</p>
        </div>
      );
    }
    return null;
  };

  const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent }: any) => {
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * (Math.PI / 180));
    const y = cy + radius * Math.sin(-midAngle * (Math.PI / 180));

    return (
      <text
        x={x}
        y={y}
        fill={chartColors.text}
        textAnchor="middle"
        dominantBaseline="central"
        className="text-sm font-medium"
      >
        {`${(percent * 100).toFixed(0)}%`}
      </text>
    );
  };

  return (
    <div
      className="min-h-screen p-6 md:p-8 font-sans"
      style={{
        background: isDarkMode
          ? "linear-gradient(to bottom right, #111827, #1F2937)"
          : "linear-gradient(to bottom right, #F3F4F6, #E5E7EB)",
      }}
    >
      <Header name="Project Management Dashboard" />
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div
          className="rounded-xl shadow-lg p-6 transition-all duration-300 hover:shadow-2xl"
          style={{ backgroundColor: chartColors.cardBg }}
        >
          <h3 className="text-xl font-bold mb-4" style={{ color: chartColors.text }}>
            Task Priority Distribution
          </h3>
          <ResponsiveContainer width="100%" height={350}>
            <BarChart data={taskDistribution}>
              <defs>
                <linearGradient id="barGradientLight" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="0%" stopColor="#3B82F6" stopOpacity={0.9} />
                  <stop offset="100%" stopColor="#60A5FA" stopOpacity={0.7} />
                </linearGradient>
                <linearGradient id="barGradientDark" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="0%" stopColor="#60A5FA" stopOpacity={0.9} />
                  <stop offset="100%" stopColor="#3B82F6" stopOpacity={0.7} />
                </linearGradient>
              </defs>
              <CartesianGrid strokeDasharray="3 3" stroke={chartColors.barGrid} />
              <XAxis dataKey="name" stroke={chartColors.text} tick={{ fontSize: 14 }} />
              <YAxis stroke={chartColors.text} tick={{ fontSize: 14 }} />
              <Tooltip content={<CustomTooltip />} />
              <Legend wrapperStyle={{ color: chartColors.text, fontSize: 14 }} />
              <Bar
                dataKey="count"
                radius={[8, 8, 0, 0]}
                animationDuration={1000}
              >
                {taskDistribution.map((entry, index) => (
                  <Cell
                    key={`bar-${index}`}
                    fill={BAR_COLORS[index % BAR_COLORS.length]}
                    className="transition-all duration-200 hover:opacity-80"
                  />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
        <div
          className="rounded-xl shadow-lg p-6 transition-all duration-300 hover:shadow-2xl"
          style={{ backgroundColor: chartColors.cardBg }}
        >
          <h3 className="text-xl font-bold mb-4" style={{ color: chartColors.text }}>
            Project Status
          </h3>
          {projectStatus.length === 1 && projectStatus[0].name === "Completed" && (
            <p className="text-center mb-4 text-sm" style={{ color: chartColors.text }}>
              All projects are completed. Add new projects or update existing ones to mark them as active (endDate in the future or null).
            </p>
          )}
          <ResponsiveContainer width="100%" height={350}>
            <PieChart>
              <Pie
                dataKey="count"
                data={projectStatus}
                fill={chartColors.pieFill}
                label={renderCustomizedLabel}
                labelLine={false}
                animationDuration={1000}
              >
                {projectStatus.map((entry: { name: string; count: number }, index: number) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={COLORS[index % COLORS.length]}
                  />
                ))}
              </Pie>
              <Tooltip content={<CustomTooltip />} />
              <Legend wrapperStyle={{ color: chartColors.text, fontSize: 14 }} />
            </PieChart>
          </ResponsiveContainer>
        </div>
        <div
          className="rounded-xl shadow-lg p-6 md:col-span-2 transition-all duration-300 hover:shadow-2xl"
          style={{ backgroundColor: chartColors.cardBg }}
        >
          <h3 className="text-xl font-bold mb-4" style={{ color: chartColors.text }}>
            Your Tasks
          </h3>
          <div style={{ height: 450, width: "100%" }}>
            <DataGrid
              rows={tasks}
              columns={taskColumns}
              checkboxSelection
              loading={tasksLoading}
              getRowClassName={() => "data-grid-row"}
              getCellClassName={() => "data-grid-cell"}
              className={dataGridClassNames}
              sx={{
                ...dataGridSxStyles(isDarkMode),
                "& .MuiDataGrid-row": {
                  transition: "background-color 0.2s",
                  "&:hover": {
                    backgroundColor: isDarkMode ? "#4B5563" : "#F3F4F6",
                  },
                },
                "& .MuiDataGrid-cell": {
                  color: isDarkMode ? "#E5E7EB" : "#111827",
                },
                "& .MuiDataGrid-columnHeaders": {
                  backgroundColor: isDarkMode ? "#1F2937" : "#F9FAFB",
                  color: isDarkMode ? "#E5E7EB" : "#111827",
                },
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default HomePage;